@import url("https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@400;500;552&family=Edu+NSW+ACT+Foundation:wght@500&family=Edu+TAS+Beginner&family=Roboto:wght@500&display=swap");
body {
  margin: 0;
  padding: 0;
  font-family: "Baloo Tamma 2", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(247, 227, 250);
  font-size: 1.2rem;
}
/*************************  Nav  *************************/
.nav-container {
  display: flex;
  position: fixed;
  height: 4rem;
  background-color: #fff;
  align-items: center;
  justify-content: space-around;
  text-decoration: none;
  width: 100%;
  border-bottom: 2px rgb(247, 227, 250) solid;
}
.nav-container > h3 {
  margin: auto 2%;
}
.nav-list {
  display: flex;
  justify-content: space-around;
  margin: auto 2% auto 60%;
  align-items: flex-end;
  color: black;
}
/*************************  Home  ************************/
.section-1 {
  display: flex;
}
.frontend-image {
  padding: 60px;
  border-radius: 2rem;
}
.frontend-image img {
  border-radius: 5rem;
  margin: 60px auto 0px;
}
h2 {
  text-align: center;
}
.bio-text {
  line-height: 1.9rem;
}
.about {
  margin-top: 13rem;
  width: 25rem;
  height: 15rem;
  border-radius: 2rem;
  box-shadow: 5rem;
  background-color: white;
  box-shadow: 0px 0px 8px 0px;
  padding: 1rem;
}
/***************************************************************/
.section-2 {
  padding: 1rem 9rem;
}
/*****************************   Skills   ********************************/

h1 {
  text-align: center;
}

.skill-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 90rem;
  height: auto;
}

.img {
  margin: 2rem;
  width: 8rem;
  height: 8rem;
  padding: 0.5rem;
  box-shadow: 0px 2px 10px 0px;
  border-radius: 2rem;
}

/**************************  Projects  ***********************/

.project {
  height: 300px;
  justify-content: center;
  margin: 20px auto 150px;
  display: flex;
  position: relative;
}
.project-items {
  justify-content: center;
  height: inherit;
  position: relative;
  z-index: -1;
  width: 27%;
  margin: auto;
  padding: 2%;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0px 3px 10px 0px;
  text-wrap: wrap;
}
.project-items > a {
  text-decoration: none;
}
